import React, {Component} from 'react';

class App extends Component
{
    state = {
        flags: {
            Netherlands: '',
            Iran: '',
            Argentina: '',
            Denmark: '',
            Spain: '',
            Croatia: '',
            Brazil: '',
            Uruguay: '',
            England: '',
            Ecuador: '',
            France: '',
            Mexico: '',
            Belgium: '',
            Germany: '',
            Portugal: '',
            Switzerland: '',
        },
    }
    render() {
        return (
            <>
                <div className="match-up">
                    <div className="flag">
                        <span>{this.state.flags[this.props.item.team1.name]}</span>
                    </div>
                    <div className="name">
                        <span>{this.props.item.team1.percent} %</span>
                        <span>{this.props.item.team1.name}</span>
                    </div>
                </div>
                <hr/>
                <div className="match-down">
                    <div className="flag">{this.state.flags[this.props.item.team2.name]}</div>
                    <div className="name">
                        <span>{this.props.item.team2.percent} %</span>
                        <span>{this.props.item.team2.name}</span>
                    </div>
                </div>
            </>
        )
    }
}

export default function Match(props) {
    return <App {...props} />;
}