import React, {Component} from 'react';
import html2canvas from "html2canvas";
import axios from 'axios';
import FormData from 'form-data';

import Match from './Match';

class App extends Component
{
    state = {
        modal: false,
        flags: {
            Netherlands: '',
            Iran: '',
            Argentina: '',
            Denmark: '',
            Spain: '',
            Croatia: '',
            Brazil: '',
            Uruguay: '',
            England: '',
            Ecuador: '',
            France: '',
            Mexico: '',
            Belgium: '',
            Germany: '',
            Portugal: '',
            Switzerland: '',
        },
        r16l: [
            {
                team1: {name: 'Netherlands', percent: '66.1', n_group: 'qfl', n_index: 0, n_item: 'team1'},
                team2: {name: 'Iran', percent: '33.9', n_group: 'qfl', n_index: 0, n_item: 'team1'},
            },
            {
                team1: {name: 'Argentina', percent: '61.1', n_group: 'qfl', n_index: 0, n_item: 'team2'},
                team2: {name: 'Denmark', percent: '38.9', n_group: 'qfl', n_index: 0, n_item: 'team2'},
            },
            {
                team1: {name: 'Spain', percent: '57.7', n_group: 'qfl', n_index: 1, n_item: 'team1'},
                team2: {name: 'Croatia', percent: '42.3', n_group: 'qfl', n_index: 1, n_item: 'team1'},
            },
            {
                team1: {name: 'Brazil', percent: '65.1', n_group: 'qfl', n_index: 1, n_item: 'team2'},
                team2: {name: 'Uruguay', percent: '34.9', n_group: 'qfl', n_index: 1, n_item: 'team2'},
            },
        ],
        r16r: [
            {
                team1: {name: 'England', percent: '55.9', n_group: 'qfr', n_index: 0, n_item: 'team1'},
                team2: {name: 'Ecuador', percent: '44.1', n_group: 'qfr', n_index: 0, n_item: 'team1'},
            },
            {
                team1: {name: 'France', percent: '61.1', n_group: 'qfr', n_index: 0, n_item: 'team2'},
                team2: {name: 'Mexico', percent: '38.9', n_group: 'qfr', n_index: 0, n_item: 'team2'},
            },
            {
                team1: {name: 'Belgium', percent: '57.7', n_group: 'qfr', n_index: 1, n_item: 'team1'},
                team2: {name: 'Germany', percent: '42.3', n_group: 'qfr', n_index: 1, n_item: 'team1'},
            },
            {
                team1: {name: 'Portugal', percent: '65.1', n_group: 'qfr', n_index: 1, n_item: 'team2'},
                team2: {name: 'Switzerland', percent: '34.9', n_group: 'qfr', n_index: 1, n_item: 'team2'},
            },
        ],
        qfl: [
            {
                team1: {name: 'Netherlands', percent: '43.5', n_group: 'sfl', n_index: 0, n_item: 'team1'},
                team2: {name: 'Argentina', percent: '56.5', n_group: 'sfl', n_index: 0, n_item: 'team1'},
            },
            {
                team1: {name: 'Spain', percent: '41.5', n_group: 'sfl', n_index: 0, n_item: 'team2'},
                team2: {name: 'Brazil', percent: '58.5', n_group: 'sfl', n_index: 0, n_item: 'team2'},
            },
        ],
        qfr: [
            {
                team1: {name: 'England', percent: '44.3', n_group: 'sfr', n_index: 0, n_item: 'team1'},
                team2: {name: 'France', percent: '55.7', n_group: 'sfr', n_index: 0, n_item: 'team1'},
            },
            {
                team1: {name: 'Belgium', percent: '50.9', n_group: 'sfr', n_index: 0, n_item: 'team2'},
                team2: {name: 'Portugal', percent: '49.1', n_group: 'sfr', n_index: 0, n_item: 'team2'},
            },
        ],
        sfl: [
            {
                team1: {name: 'Argentina', percent: '48.4', n_group: 'final', n_index: 0, n_item: 'team1'},
                team2: {name: 'Brazil', percent: '51.6', n_group: 'final', n_index: 0, n_item: 'team1'},
            }
        ],
        sfr: [
            {
                team1: {name: 'France', percent: '48.9', n_group: 'final', n_index: 0, n_item: 'team2'},
                team2: {name: 'Belgium', percent: '51.1', n_group: 'final', n_index: 0, n_item: 'team2'},
            }
        ],
        final: [
            {
                team1: {name: 'Brazil', percent: '61.3'},
                team2: {name: 'Belgium', percent: '38.7'},
            }
        ],
        winner: {name: 'Brazil'},

        team1: 0,
        team2: 0,
        match_group: '',
        match_index: '',
        match_item: {},
    }

    exportContent = async (action) => {
        const fileName = 'World-Cup-2022-Prediction.png'
        const element = document.getElementById('content')
        const canvas = await html2canvas(element)
        const image = canvas.toDataURL("image/png", 1.0)
        // console.log(image)

        if(action === 'download') {
            this.downloadImage(image, fileName);
        } else {
            const fbShare = document.getElementById('fbShare');
            const base64Response = await fetch(image)
            const blob = await base64Response.blob()
            const file = this.blobToFile(blob, fileName)

            fbShare.innerText = 'Creating image...'
            const formData = new FormData()
            formData.append('file', file, fileName)
            const res = await axios.post('https://tmpfiles.org/api/v1/upload', formData, { headers: {'Content-Type': `multipart/form-data`} })
            const image_url = res.data.data.url
            const image_src = image_url.replace('https://tmpfiles.org', 'https://tmpfiles.org/dl')
            // console.log(image_src)

            fbShare.innerText = 'Loading Share...'
            await this.socialShare(image_src)
            fbShare.innerText = 'Facebook Share'
        }
    }

    blobToFile = (theBlob, fileName) => {
        //A Blob() is almost a File() - it's just missing the two properties below which we will add
        theBlob.lastModifiedDate = new Date();
        theBlob.name = fileName;
        return theBlob;
    }

    socialShare = async (image) => {
        window.FB.getLoginStatus(function (response) {
            // console.log(response);
            window.FB.ui({
                display: 'popup',
                method: 'share',
                media: [image],
            }, function(response){});
        })
    }
        
    downloadImage = (blob, fileName) => {
        const fakeLink = window.document.createElement("a");
        fakeLink.style = "display:none;";
        fakeLink.download = fileName;

        fakeLink.href = blob;

        document.body.appendChild(fakeLink);
        fakeLink.click();
        document.body.removeChild(fakeLink);

        fakeLink.remove();
    }

    matchClick = (group, index, item) => {
        // console.log(group, index, item)
        this.setState({modal:true})
        this.setState({match_group: group, match_index: index, match_item: item})
        this.setState({team1: item.team1.percent, team2: item.team2.percent})
    }

    handleInput = async (e) => {
        let team1 = e.target.value
        team1 = Math.round(team1 * 10) / 10
        let team2 = 100 - team1
        team2 = Math.round(team2 * 10) / 10
        this.setState({team1, team2})
    }

    save = () => {
        // console.log(this.state)
        // console.log(this.state.match_group)
        // console.log(this.state[this.state.match_group])
        // console.log(this.state[this.state.match_group][this.state.match_index])
        const team1_obj = this.state[this.state.match_group][this.state.match_index].team1
        const team2_obj = this.state[this.state.match_group][this.state.match_index].team2
        this.state[this.state.match_group][this.state.match_index].team1.percent = this.state.team1;
        this.state[this.state.match_group][this.state.match_index].team2.percent = this.state.team2;
        this.setState({modal: false})

        if(this.state.match_group !== 'final') {
            if(this.state.team1 > this.state.team2) {
                this.state[team1_obj.n_group][team1_obj.n_index][team1_obj.n_item].name = team1_obj.name
            } else {
                this.state[team2_obj.n_group][team2_obj.n_index][team2_obj.n_item].name = team2_obj.name
            }
        } else {
            if(this.state.team1 > this.state.team2) {
                this.state.winner.name = team1_obj.name
            } else {
                this.state.winner.name = team2_obj.name
            }
        }
        this.forceUpdate()
    }
    
    render() {
        var r16l_html = this.state.r16l.map((item, index) => {
            return (
                <div key={`r16l-${index}`} className="match" onClick={e=>this.matchClick('r16l', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        var r16r_html = this.state.r16r.map((item, index) => {
            return (
                <div key={`r16r-${index}`} className="match" onClick={e=>this.matchClick('r16r', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        var qfl_html = this.state.qfl.map((item, index) => {
            return (
                <div key={`qfl-${index}`} className="match" onClick={e=>this.matchClick('qfl', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        var qfr_html = this.state.qfr.map((item, index) => {
            return (
                <div key={`qfr-${index}`} className="match" onClick={e=>this.matchClick('qfr', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        var sfl_html = this.state.sfl.map((item, index) => {
            return (
                <div key={`sfl-${index}`} className="match" onClick={e=>this.matchClick('sfl', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        var sfr_html = this.state.sfr.map((item, index) => {
            return (
                <div key={`sfr-${index}`} className="match" onClick={e=>this.matchClick('sfr', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        var final_html = this.state.final.map((item, index) => {
            return (
                <div key={`final-${index}`} className="match" onClick={e=>this.matchClick('final', index, item)}>
                    <Match item={item}/>
                </div>
            )
        })
        return (
            <>
                <div className="content" id="content">
                    <h1>World Cup 2022 Prediction</h1>
                    <hr/>
                    <div className="match-title">
                        <span>Round 16</span>
                        <span>Quarter Final</span>
                        <span>Semi Final</span>
                        <span>Final</span>
                        <span>Semi Final</span>
                        <span>Quarter Final</span>
                        <span>Round 16</span>
                    </div>
                    <div className="match-content">
                        <div className="r16 left">{r16l_html}</div>
                        <div className="qf left">{qfl_html}</div>
                        <div className="sf left">{sfl_html}</div>
                        <div className="final">
                            <div className="winner">
                                {this.state.flags[this.state.winner.name]}
                                <span className="winner-label">Winner</span>
                            </div>
                            {final_html}
                        </div>
                        <div className="sf right">{sfr_html}</div>
                        <div className="qf right">{qfr_html}</div>
                        <div className="r16 right">{r16r_html}</div>
                    </div>
                    {
                        this.state.modal &&
                        <div className="modal">
                            <div className="modal-content">
                                <span className="close" onClick={e=>this.setState({modal:false})}></span>
                                <h3>Adjust the winning chance</h3>
                                <br/><hr/>
                                <div className="slider">
                                    <input name="team1" value={this.state.team1} min="0" max="100" step="0.1" type="range" onChange={this.handleInput} />
                                </div>
                                <div className="match">
                                    <div className="match-up">
                                        <div className="flag">
                                            <span>{this.state.flags[this.state.match_item.team1.name]}</span>
                                        </div>
                                        <div className="name">
                                            <span>{this.state.team1} %</span>
                                            <span>{this.state.match_item.team1.name}</span>
                                        </div>
                                    </div>
                                    <hr/>
                                    <div className="match-down">
                                        <div className="flag">
                                            <span>{this.state.flags[this.state.match_item.team2.name]}</span>
                                        </div>
                                        <div className="name">
                                            <span>{this.state.team2} %</span>
                                            <span>{this.state.match_item.team2.name}</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="control">
                                    <button className="btn-save" onClick={e=>this.save()}>Save</button>
                                </div>
                            </div>
                        </div>
                    }
                </div>
                <div className="social">
                    <button className="btn-download" onClick={e=>this.exportContent('download')}>Download Image</button>
                    <br/>
                    <button className="btn-fb-share" id="fbShare" onClick={e=>this.exportContent('share')}>Facebook Share</button>
                </div>
            </>
        )
    }
}

export default function Home(props) {
    return <App {...props} />
}